import { Injectable, Optional, Inject, InjectionToken } from '@angular/core';
import { LoadMoreMethod } from '@backbase/payment-orders-ang';
import { StopChecksJourneyConfiguration } from '../models/journey-configuration.model';
import { SchemeName } from '@backbase/data-ang/stop-checks';

export const StopChecksJourneyConfigurationToken =
  new InjectionToken<StopChecksJourneyConfiguration>(
    'StopChecksJourneyConfiguration injection token'
  );

export const stopChecksDefaultConfig: StopChecksJourneyConfiguration = {
  currencies: ['USD'],
  pageSize: 50,
  maxNavPages: 3,
  notificationDismissTime: 3,
  paginationType: LoadMoreMethod.PAGINATION,
  checksRangeNumber: 0,
  shouldDisplayHeading: true,
  schemeName: 'BBAN',
};

@Injectable()
export class StopChecksJourneyService {
  private readonly _config: StopChecksJourneyConfiguration;

  constructor(
    @Optional()
    @Inject(StopChecksJourneyConfigurationToken)
    private config: StopChecksJourneyConfiguration
  ) {
    this._config = { ...stopChecksDefaultConfig, ...(config || {}) };
  }

  get currencies(): string[] {
    return this._config.currencies;
  }

  get pageSize(): number {
    return this._config.pageSize;
  }

  get schemeName(): SchemeName {
    return this._config.schemeName;
  }

  get maxNavPages(): number {
    return this._config.maxNavPages;
  }

  get notificationDismissTime(): number {
    return this._config.notificationDismissTime;
  }

  get paginationType(): LoadMoreMethod {
    return this._config.paginationType;
  }

  get checksRangeNumber(): number {
    return this._config.checksRangeNumber;
  }

  get shouldDisplayHeading(): boolean {
    return this._config.shouldDisplayHeading;
  }

  get configuration(): StopChecksJourneyConfiguration {
    return this._config;
  }
}
